@use '../global/variables' as g;

.main-logo {
    display: none;

    &:hover {
        .main-logo__small-rays {
            transform-origin: center;
            animation-play-state: paused;
            animation: logo-rotate-1 20s ease-in-out infinite;
        }
    }


    @keyframes logo-rotate-1 {
        0% {
            transform: rotate(0deg);
        }

        50% {
            transform: rotate(360deg);
        }

        100% {
            transform: rotate(0deg);
        }
    }
}