@font-face {
  font-family: "Shadows Into Light";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("./fonts/ShadowsIntoLight-Regular.woff2") format("woff2"), url("./fonts/ShadowsIntoLight-Regular.woff") format("woff");
}
/* Reset CSS */
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

html {
  height: 100%;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  min-height: 100%;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

menu:not(article menu),
ol:not(article ol),
ul:not(article ul) {
  list-style: none;
}

menu,
ol,
ul {
  padding-left: 0;
}

article ol,
article ul {
  list-style-position: inside;
}

label,
button,
select,
summary,
[type=radio],
[type=submit],
[type=checkbox] {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

/* Global Styles */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  margin: 0 auto;
  padding-inline: 15px;
}
@media (min-width: 1200px) {
  .wrapper {
    padding-inline: 60px;
  }
}

/* Intro */
.intro {
  text-align: center;
}

/* Upload Link */
.uploadLink {
  display: block;
  margin: 25px 0;
  width: fit-content;
  border: 5px solid navy;
  border-radius: 25px;
  font-size: 36px;
  line-height: 54px;
  font-weight: bold;
  padding: 30px;
  color: navy;
  text-decoration: none;
}

.page {
  font-size: 14px;
  line-height: 26px;
  font-family: "Shadows Into Light", cursive;
  margin: auto;
}
.page__body {
  min-width: 320px;
  min-height: 100%;
  margin: auto;
  max-width: 100%;
  background-color: #1f1f1f;
  color: #ffffff;
}

.main-nav {
  padding-block: 25px;
}
@media (min-width: 768px) {
  .main-nav {
    padding-block: 50px;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    padding-block-end: 70px;
    display: grid;
    grid-template-columns: 1fr;
  }
}
.main-nav__items {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
@media (min-width: 768px) {
  .main-nav__items {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 10px;
  }
}
.main-nav__item {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  line-height: 1.5;
  font-weight: 700;
  text-align: center;
}
@media (min-width: 768px) {
  .main-nav__item {
    font-size: 28px;
    line-height: 1.4;
  }
}
@media (min-width: 1200px) {
  .main-nav__item {
    font-size: 36px;
    line-height: 1.3;
  }
}
@media (min-width: 1440px) {
  .main-nav__item {
    font-size: 40px;
    line-height: 1.2;
  }
}
@media (max-width: 1199px) {
  .main-nav__item--desktop {
    display: none;
  }
}

.main-logo {
  display: none;
}
.main-logo:hover .main-logo__small-rays {
  transform-origin: center;
  animation-play-state: paused;
  animation: logo-rotate-1 20s ease-in-out infinite;
}
@keyframes logo-rotate-1 {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

/* Photo Gallery */
.gallery {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
}
@media (min-width: 768px) {
  .gallery {
    display: block;
    columns: 2;
    column-gap: 40px;
  }
}
@media (min-width: 1200px) {
  .gallery {
    columns: 3;
  }
}
.gallery__photo {
  margin-block-end: 40px;
}
.gallery__img {
  border-radius: 5px;
  margin: 0 auto;
}
.gallery__photo:hover {
  cursor: pointer;
  transition: 1s ease-out;
  transform: scale(1.03);
}

.photo-popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
}
.photo-popup--visible {
  display: block;
}
.photo-popup__image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 90%;
  max-height: 90%;
}

.about-me {
  border: solid #ffffff 5px;
  border-radius: 6%;
  text-align: center;
  background-color: #1f1f1f;
  position: relative;
  background-color: transparent;
  overflow: hidden;
}
@media (min-width: 1200px) {
  .about-me {
    order: 3;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 20px;
    padding-block-start: 80px;
    padding-block-end: 240px;
    padding-inline: 40px;
  }
}
.about-me:hover {
  transition: 1s ease-out;
  transform: scale(1.03);
}
.about-me:hover .about-me__background {
  transition: 1s ease-out;
  transform: scale(0.97);
}
.about-me__background {
  position: absolute;
  top: -20px;
  left: -20px;
  width: 110%;
  height: 110%;
  background-image: url("about-me-background.jpg");
  background-size: cover;
  background-color: #1f1f1f;
  background-blend-mode: overlay;
  z-index: -1;
}
.about-me__title {
  font-size: 40px;
  line-height: 1.2;
}
@media (min-width: 1200px) {
  .about-me__title {
    margin-block-end: 30px;
  }
}
.about-me__description {
  font-size: 18px;
  line-height: 1.4;
  font-weight: 700;
  text-transform: uppercase;
  margin-block-end: 5px;
}
@media (min-width: 1200px) {
  .about-me__contacts {
    display: flex;
    justify-content: space-around;
    column-gap: 36px;
    margin-block-start: 80px;
  }
}
.about-me__link {
  display: flex;
  align-items: center;
  font-size: 18px;
  line-height: 1.4;
}
.about-me__link-icon {
  margin-inline-end: 8px;
  width: 32px;
  height: 32px;
}