/* Global Styles */
@use './variables' as g;

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.wrapper {
    margin: 0 auto;
    padding-inline: 15px;

    @media (min-width: 1200px) {
        padding-inline: 60px;
    }
}

/* Intro */
.intro {
    text-align: center;
}

/* Upload Link */
.uploadLink {
    display: block;
    margin: 25px 0;
    width: fit-content;
    border: 5px solid navy;
    border-radius: 25px;
    font-size: 36px;
    line-height: 54px;
    font-weight: bold;
    padding: 30px;
    color: navy;
    text-decoration: none;
}