@use '../global/variables' as g;

.main-nav {
    padding-block: 25px;

    @media (min-width: g.$tablet-width) {
        padding-block: 50px;
    }

    @media (min-width: g.$desktop-width) {
        padding-block-end: 70px;
        display: grid;
        grid-template-columns: 1fr;
    }

    &__items {
        display: flex;
        flex-direction: column;
        gap: 15px;

        @media (min-width: g.$tablet-width) {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
            gap: 10px;
        }
    }

    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 30px;
        line-height: 1.5;
        font-weight: 700;
        text-align: center;

        @media (min-width: g.$tablet-width) {
            font-size: 28px;
            line-height: 1.4;
        }

        @media (min-width: g.$desktop-width) {
            font-size: 36px;
            line-height: 1.3;
        }

        @media (min-width: g.$desktop-large-width) {
            font-size: 40px;
            line-height: 1.2;
        }

        &--desktop {
            @media (max-width: g.$max-tablet-width) {
                display: none;
            }
        }
    }
}